<template>
    <div>
        <div class="card centered"
             v-if="loading">
            <app-pulse-loader></app-pulse-loader>
        </div>
        <div class="card  background"
             v-if="!loading">

            <!--      New Videos -->
            <template v-if="newVideos.length>0">

                <div class="alert alert-warning"
                     style="cursor: pointer">
                    <div class="alert-heading">
                        <strong style="font-size: x-large">!!!New Tutorial</strong>
                        <app-video v-for="video in newVideos"
                                   :video="video"></app-video>
                    </div>
                </div>

            </template>

            <!--      Old Videos-->
            <template v-if="videos.length>0">

                <div class="card bg-light mb-3">
                    <div class="card-body">
                        <app-video v-for="video in videos"
                                   :video="video"></app-video>
                    </div>
                </div>

            </template>
        </div>
    </div>
</template>

<script>
    import PulseLoader from 'vue-spinner/src/PulseLoader.vue';
    import Video from './Video';
    import axios from 'axios';

    const baseURL='https://spam-tutorial-result.firebaseio.com/';

    export default {
        name: 'ChapterVideo',
        props: ['chapterNameKey'],
        data(){
            return {
                loading: true,
                videos: [],
                newVideos: []
            };
        },
        components: {
            appVideo:Video,
            appPulseLoader:PulseLoader
        },
        created() {
            this.fetchAllVideo();
        },
        methods: {
            fetchAllVideo(){
                this.videos=[];
                this.newVideos=[];

                const link=baseURL+this.chapterNameKey+".json";

                axios.get(link)
                    .then(res=>{

                        for(let key in res.data){
                            let video={
                                videoTitle: '',
                                videoLink: '',
                                downloadLink: ''
                            };

                            video.videoTitle=res.data[key].videoTitle;
                            video.videoLink=res.data[key].videoLink;
                            video.downloadLink=res.data[key].downloadLink;

                            this.videos.push(video);

                            let d=new Date(res.data[key].alertDate);
                            if(d>(new Date()))
                                this.newVideos.push(video);
                        }

                        this.loading=false;
                    }).catch(error=>{
                    //console.log(error);
                });
            }
        }
    }
</script>

