<template>
    <div>
        <div class="card centered"
             v-if="loading">
            <app-pulse-loader></app-pulse-loader>
        </div>

        <div class="card background"
             v-if="!loading">
            <template v-for="chapter in chapters">

                <div class="card bg-light mb-3">
                    <div class="card-header"
                         @click="chapter.expand=!chapter.expand"
                         style="font-family: 'Galada', cursive; cursor: pointer; font-size: xx-large">
                        {{chapter.name}}
                    </div>
                    <app-chapter-video v-if="chapter.expand"
                                       :chapter-name-key="chapter.key"></app-chapter-video>
                </div>

            </template>
        </div>
    </div>
</template>

<script>
    import PulseLoader from 'vue-spinner/src/PulseLoader.vue';
    import ChapterVideo from "./ChapterVideo";
    import axios from 'axios';

    const baseURL='https://spam-tutorial-result.firebaseio.com/';

    export default {
        name: 'Tutorial',
        data(){
            return {
                chapters: [],
                loading: true
            }
        },
        components: {
            appChapterVideo: ChapterVideo,
            appPulseLoader: PulseLoader
        },
        created() {
            this.getAllChapter();
        },
        methods: {
            getAllChapter(){
                this.chapters=[];
                const link=baseURL+"chapters.json";

                //console.log(link);

                axios.get(link)
                    .then(res=>{

                        for(let key in res.data){
                            let chapter={
                                name: '',
                                key: key,
                                expand: false
                            };

                            chapter.name=res.data[key].chapter;
                            this.chapters.push(chapter);
                        }

                        this.loading=false;
                        //console.log(this.chapters);
                    }).catch(error=>{
                        //console.log(error);
                });
            }
        }
    }
</script>

